<template>
  <div class="citysearch">
    <input v-model="keyword" type="text" placeholder="输入你要搜索的城市">
    <!-- v-model="keyword"实现双向数据绑定 -->
    <div class="searchlist wrapper" v-show="keyword" ref="wrapper">
      <ul  >
        <li class="item" @click="handleClick" v-for="(item,index) of list" :key="index">{{item.name}}</li>
        <li class="item" v-show="!list.length">没有你输入的城市</li>
      </ul>
    </div>
  </div>
</template>
<script>
import BScroll from "better-scroll";
export default {
  name: "CitySearch",
  props: ["cities"],
  data() {
    return {
      keyword: "",
      list: []
    };
  },
  watch: {
    keyword() {
      var result = [];
      if (!this.keyword) {
        result = [];
      } else {
        for (var i in this.cities) {
          this.cities[i].forEach(ele => {
            if (
              ele.name.startsWith(this.keyword) ||
              ele.spell.startsWith(this.keyword)
            ) {
              result.push(ele);
            }
          });
        }
      }
      this.list = result;
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.scroll = new BScroll(this.$refs.wrapper, {});
    });
  },
  methods: {
    handleClick(event){
      this.$store.commit('changeCity',event.target.innerText)
      this.$router.push("/")
      this.keyword=""
    }
  },
};
</script>
<style lang="scss" scoped>
.citysearch {
  position: fixed;
  top: 90px;
  left: 0;
  right: 0;
  border-top: 1px solid rgba(250, 248, 248, 0.459);
  height: 88px;
  padding-left: 20px;
  padding-right: 20px;
  background: #00badc;
  line-height: 88px;
  text-align: center;
  z-index: 100;
}
.wrapper {
 
  width: 100%;
  max-height: 1000px;
  overflow: hidden;
  position: relative;
}
.citysearch input {
  width: 90%;
  border-radius: 15px;
  border: none;
  outline: none;
  height: 66px;
  padding-left: 20px;
}
.searchlist {
  position: absolute;
  top: 96px;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: -1;
  height: 1000px;
  background: #fff;
}
.item {
  line-height: 54px;
  color: #666;
  border-bottom: 1px solid #eee;
  text-align: left;
  padding-left: 40px;
}
</style>