<template>
  <div class="home">
    <div v-if="isLoading">
      <home-head></home-head>
      <home-swiper :banner="banner"></home-swiper>
      <home-icons :icons="icons"></home-icons>
      <head-recomment :recommendList="recommendList"></head-recomment>
      <head-weekend :weekendList="weekendList"></head-weekend>
    </div>
     <loading v-if="!isLoading"></loading>
  </div>
</template>

<script>
// @ is an alias to /src
import HomeHead from "./components/Head";
import HomeSwiper from "./components/swiper";
import { HomeModel } from "../../models/HomeModel";
import HomeIcons from "./components/icons";
import HeadRecomment from "./components/Recomment";
import HeadWeekend from "./components/Weekend";
import loading from "../home/components/Loading";
const homeModel = new HomeModel();
export default {
  name: "home",
  components: {
    HomeHead,
    HomeSwiper,
    HomeIcons,
    HeadRecomment,
    HeadWeekend,
    loading
  },
  data() {
    return {
      banner: [],
      icons: [],
      recommendList: [],
      weekendList: [],
      currentCity: "",
      isLoading: false
    };
  },
  mounted() {
    this.currentCity = this.city;
    homeModel.getHome(this.city).then(res => {
      let data = res.data.data;
      let { banner, icons, recommendList, weekendList } = data;
      this.banner = banner;
      this.icons = icons;
      this.recommendList = recommendList;
      this.weekendList = weekendList;
      this.isLoading = true;
    });
  },

  activated() {
    if (this.currentCity != this.city) {
      this.currentCity = this.city;
      homeModel.getHome(this.city).then(res => {
        let data = res.data.data;
        let { banner, icons, recommendList, weekendList } = data;
        this.banner = banner;
        this.icons = icons;
        this.recommendList = recommendList;
        this.weekendList = weekendList;
      });
    }
  },
  computed: {
    city() {
      return this.$store.state.city;
    }
  }
};
</script>
