<template>
  <div class="detail">
    <deatail-nav v-if="isShowNav" :style="opacitys"></deatail-nav>
    <img :src="bannerImg" class="banner" @click="handleClick">
    <i class="iconfont bc">&#xe62f;34</i>
    <p class="title">知音号游船</p>
    <router-link tag="i" to="/" class="iconfont bg" >&#xe604;</router-link>
    
    <detail-fade>
      <detail-gallary slot="fade" :gallaryImgs="gallaryImgs" v-show="isGallary" @toggle="hide"></detail-gallary>
    </detail-fade>

    <detail-ticket :categoryList="categoryList"></detail-ticket>
  </div>
</template>
<script>
import { DetailModel } from "../../models/DetailModel";
import DetailGallary from "../detail/components/gallary";
import DeatailNav from "../detail/components/nav";
import DetailTicket from "../detail/components/ticket";
import DetailFade from '../detail/components/animations'
const detailmodel = new DetailModel();
export default {
  name: "detail",
  data() {
    return {
      bannerImg: "",
      categoryList: [],
      gallaryImgs: [],
      sightName: "",
      isGallary: false,
      isShowNav: false,
      opacitys: {
        opacity: 0
      }
    };
  },
  methods: {
    handleClick() {
      this.isGallary = true;
    },
    hide() {
      this.isGallary = false;
    },
    handleScroll() {
      var top=document.documentElement.scrollTop||document.body.scrollTop;
      if(top>0){
          this.isShowNav=true
          var opacity=top/300;
          if(opacity>1){
              opacity=1
          }
          this.opacitys.opacity=opacity;
      }else{
          this.isShowNav=false
      }
    }
  },
  destroyed(){
     window.removeEventListener("scroll",this.handleScroll)
  },
  components: {
    DetailGallary,
    DeatailNav,
    DetailTicket,
    DetailFade
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll);
    var id = this.$route.params.id;
    detailmodel.getDetail(id).then(res => {
      let data = res.data.data;
      console.log(data);
      let { bannerImg, categoryList, gallaryImgs, sightName } = data;
      this.bannerImg = data.bannerImg;
      this.categoryList = data.categoryList;
      this.gallaryImgs = data.gallaryImgs;
      this.sightName = data.sightName;
    });
  }
};
</script>
<style lang="scss" scoped>
.detail{height: 2000px;}
.banner {
  width: 100%;
  height: 400px;
  position: relative;
}
.bg {
  font-size: 40px;
  position: absolute;
  left: 20px;
  top: 5px;
  color: #fff;
  background: #333;
  width: 70px;
  height: 70px;
  line-height: 70px;
  text-align: center; 
  border-radius: 50%;
}
.bc{
  position: absolute;
  left: 50px;
  top:260px;
  width: 80px;
  height: 35px;
  line-height: 35px;
  text-align: center;
  background: #eee;
  border-radius: 20px;
}
.title{
  font-size: 28px;
  color: #fff;
  position: absolute;
  left: 20px;
  top:300px;
}
</style>
