<template>
  <div>
    <ul class="wordlist">
      <li @click="handleClick" v-for="(item,index) of cities" :key="index">{{index}}</li>
    </ul>
  </div>
</template>
<script>
export default {
  name: "CityWord",
  props:{
      cities:{
          type:Object
      },
  },
methods: {
  handleClick(event){
    this.$emit("change",event.target.innerText)
  }
},
};
</script>
<style lang="scss">
.wordlist {
  position: absolute;

  right: 10px;
  top: 240px;
  width: 40px;
  height:800px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #00badc;
  line-height: 40px;
}
</style>