<template>
  <div class="city">
    <city-head></city-head>
    <city-search :cities="cities"></city-search>
    <city-list :hotCities="hotCities" :cities="cities" :letter="letter"></city-list>
    <city-word :cities="cities" @change="handleWord"></city-word>
  </div>
</template>
<script>
import CityHead from "../city/components/cityhead";
import CitySearch from "../city/components/search";
import CityList from "../city/components/citylist";
import CityWord from '../city/components/word'
import { CityModel } from "../../models/CityModel";
const citymodel = new CityModel();
export default {
  name: "city",
  data() {
    return {
      hotCities: [],
      cities: {},
      letter:""
    };
  },
  components: {
    CityHead,
    CitySearch,
    CityList,
    CityWord
  },
  mounted() {
    citymodel.getCity().then(res => {
      var data = res.data.data;
      var { cities, hotCities } = data;
      this.hotCities = hotCities;
      this.cities = cities;
    //   console.log(cities);
    });
  },
  methods: {
    handleWord(word){
        this.letter=word
    }
  },
};
</script>
