<template>
  <div class="wrapper" ref="wrapper">
    <div>
      <div>
        <p class="citytitle">当前城市</p>
        <div class="addlist">
          <button class="btn current">{{this.$store.state.city}}</button>
        </div>
      </div>

      <div>
        <p class="citytitle">热门城市</p>
        <div class="addlist">
          <button class="btn" @click="handleClick(item.name)" v-for="(item,index) of hotCities" :key="index">{{item.name}}</button>
        </div>
      </div>

      <div class="leter" v-for="(item,index) of cities" :key="index">
        <p class="leterlist" :ref="index">{{index}}</p>
        <div @click="handlejump" v-for="(items,inx) of item" :key="inx">
          <p class="item">{{items.name}}</p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import BScroll from "better-scroll";
export default {
  name: "CityList",
  props: {
    cities: {
      type: Object
    },
    hotCities: {
      type: Array
    },
    letter: {
      type: String
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.scroll = new BScroll(this.$refs.wrapper, {});
    });
  },
  watch: {
    letter() {
      var element = this.$refs[this.letter][0];
      //  console.log(this.letter)
      this.scroll.scrollToElement(element);
    }
  },
  methods: {
    handleClick(city){
       this.$store.commit("changeCity",city)
       this.$router.push("/")
    },
   handlejump(event){
      this.$store.commit('changeCity',event.target.innerText)
      this.$router.push("/")
   }
  },
};
</script>
<style lang="scss" scoped>
.wrapper {
  margin-top: 190px;
  width: 100%;
  max-height: 1000px;
  overflow: hidden;
  position: relative;
}
.citytitle {
  line-height: 60px;
  background: #eee;
  padding-left: 20px;
  font-size: 28px;
}
.leter {
  padding-left: 20px;
  line-height: 60px;
}
.addlist {
  padding: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-items: center;
  grid-row-gap: 10px;
}
.btn {
  width: 200px;
  line-height: 50px;
  border: 1px solid #ccc;
  text-align: center;
  border-radius: 15px;
}
.item {
  line-height: 54px;
  padding-left: 20px;
  color: #666;
  border-bottom: 1px solid #eee;
}
.current{
  background: #ccc;
}

</style>